@charset "utf-8";

* {
	margin: 0;
	padding: 0
}

.fl {
	float: left
}

.fr {
	float: right
}

.clear {
	clear: both
}

a {
	text-decoration: none
}

body {
	background: #edf8fa
}

.new_header {
	background: #fff;
	border-bottom: solid 1px #b7c0c1;
	height:40px;
	.header_b{
		margin: 0 auto;
		width: 980px;
		color: #fff;
		height:40px;
		line-height: 40px;
		font-size: 12px;
		img{
			vertical-align: middle;
		}
		.fl img{
			width: 20px;
			margin-right: 5px;
			&.verM{
				vertical-align: -2px;
			}
			
		}
		a{
			color: #666;
		}
		span a{
			color: #666;
			&:hover{
				color: #666;
			}
		}
		.person-con{
			text-align: right;
			position: relative;
			z-index: 888;
			a{
				padding-left: 10px;
				&:hover{
					color: #666;
				}	
			}
			.more_nav{
				background: url(/_resources/mm/css/img/new_pc/sec-header.png) no-repeat 40px 20px;
				height:40px;
				width: 44px;
				display: inline-block;
				text-align: left;
			}
			.hover_nav {
				background: url(/_resources/mm/css/img/new_pc/sec-header.png) no-repeat 40px 40px;
				background-position: 0 -20px;
				height:40px;
				width:40px;
				display:inline-block;
				color: #666;
				text-align:left;
			}
		}
	}
}
.pd_10 {
	padding: 10px 3px 0 8px
}


.more_box {
	position: absolute;
	top: 31px;
	right: -1px;
	width: 170px;
	text-align: left;
	background: #c1e7ec;
	z-index: 9999;
	border: solid 1px #8cd;
	border-top: 0;
	.show_self {
		padding: 3px 10px;
		border-bottom: solid 1px #666;
		a{
			padding-left: 0!important;
		}
	}
	li{
		float: left;
		width: 30px;
		padding-right: 10px;
		a{
			padding: 0!important;
			color: #49a!important;
		}
	}
}

.new_wrap {
	overflow: hidden;
	background: none!important;
	position: relative;
	z-index: 1;
	.wrap_con {
		margin: 0 auto;
		width: 980px;
		overflow: hidden;
		.logo{
			padding: 10px 0 10px 0;
			overflow: hidden;
		}
		.new-nav-c {
			border: solid 1px #00738b;
			height: 40px;
			overflow: hidden;
			.new_nav {
				background: url(/_resources/mm/css/img/new_pc/nav-bg.jpg) repeat;
				height: 38px;
				border: solid 1px #02a9bc;
				border-top: solid 1px #9fe2e8;
				padding: 0 20px 0 30px;
				position: relative;
				a{
					color: #fff;
					font-size: 14px;
					font-weight: bold;
					line-height: 41px;
					&:hover{
						color: #fff;
					}
				}
				span{
					padding: 0 11px 0 16px;
					color: #fff;
				}
				.index_bg {
					position: absolute;
					left: 0;
					top: -3px;
					background: url(/_assets/yy/yy_nav_bg.png) no-repeat;
					width: 85px;
					text-align: center;
					height: 44px;
					padding-top: 3px;
					a {
						color: #fff;
						display: block;
					}
				}
			}
		}
	}
}

.mgl_20 {
	margin-left: 0!important
}

.mgb_18 {
	margin-bottom: 0!important
}

.search-top {
	width: 198px;
	overflow: hidden;
	height: 26px;
	margin-top: 6px!important;
	top: 0;
	right: 15px;
	position: absolute;
	background: #fff;
	border: solid 1px #8cd
}

.search-top form {
	position: relative
}

.lst {
